import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage-angular';
import { BehaviorSubject } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Help } from '../models/Help';
import { map } from 'rxjs/operators';
@Injectable({
  providedIn: 'root'
})
export class FormService {
  public form: BehaviorSubject<Help> = new BehaviorSubject(new Help());
  public optionChosed: BehaviorSubject<any> = new BehaviorSubject(null);
  constructor(private storage: Storage,public  http: HttpClient) {
    this.loadForm();
  }
   async loadForm(){
    await this.storage.get('selected').then
   ( select =>{
      if(select){
        this.optionChosed.next(select);
      }
      else{
        this.optionChosed.next(false);
      }
    }

   );
  await this.storage.get('form').then
   (form =>{
      if(form !=null){
        this.form.next(form);
      }
      else{
        this.form.next(new Help());
      }
    });
}
 getSelected(){
  return this.optionChosed.asObservable();
}
getPreguntas(){
  return this.http.get('../../assets/datos/form.json');

}
async saveForm(form: Help){

    const latlng = await this.storage.get('latlng');
    const lat = latlng[0];
    const lng = latlng[1];
    this.form.getValue().victim = form.victim;
    this.form.getValue().reason = form.reason;
    this.form.getValue().agressor = form.agressor;
    this.form.getValue().agressorGender = form.agressorGender;
    this.form.getValue().age = form.age;
    this.form.getValue().gender = form.gender;
    this.form.getValue().place = form.place;
    this.form.getValue().latitude = lat;
    this.form.getValue().longitude= lng;
    console.log(this.form.getValue());
    await this.storage.set('form',form);
    this.form.next(this.form.getValue());
    return Promise.resolve(true);
}

async setPerfil(perfil: any){
  const latlng = await this.storage.get('latlng');
  console.log(latlng);
  const lat = latlng[0];
  const lng = latlng[1];
  console.log(this.form.getValue());
   this.form.getValue().name=perfil.nombre;
   this.form.getValue().phone=perfil.telefono;
   this.form.getValue().email=perfil.correo;
   this.form.getValue().address=perfil.direccion;
   this.form.getValue().latitude = lat;
   this.form.getValue().longitude= lng;
   console.log(this.form.getValue());
   this.form.next(this.form.getValue());
   await this.storage.set('form',this.form.getValue());
   return Promise.resolve(true);
}

getForm(){
  return this.form.asObservable();
}
}
